import { useEffect, useState } from 'react';
import { ShareApi, ShareSaveApi } from 'utils/adminApi';
import { Button, Upload, message, Form, Input, Space, Card } from 'antd';
import { PlusOutlined, MinusCircleOutlined } from '@ant-design/icons';

const Share = () => {
  const [imageUrl, setImageUrl] = useState(null);
  const [bgUrl, setbgUrl] = useState(null);
  const [form] = Form.useForm();

  useEffect(() => {
    ShareApi().then((res) => {
      setImageUrl(res.data.imageUrl);
      setbgUrl(res.data.bgUrl);
			form.setFieldsValue({rules: res.data.rules})
    });
  }, []);

  const onChange = (info, type) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
      if (type === 'img') {
        setImageUrl(info.file.response);
      } else if (type === 'bg') {
        setbgUrl(info.file.response);
      }
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinish = (value) => {
    ShareSaveApi({ imageUrl, bgUrl, ...value }).then((res) => {
      message.success('保存成功');
    });
  };
  return (
    <Card title="分享页面设置" bordered={false} style={{ width: 600 }}>
      <Form
        name="control-hooks"
        onFinish={onFinish}
				form={form}
      >
        <Form.Item label="页面横幅">
          <Upload
            className="flex justify-center items-center"
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__web_token')
                ? { Authorization: `Bearer ${localStorage.__web_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'img')}
          >
            {imageUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + imageUrl}
                alt="avatar"
                className=" w-48 h-48 bg-red-400"
              />
            ) : (
              <div className="flex justify-center items-center w-48 h-48 bg-red-400">
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>
        <Form.Item label="居中背景">
          <Upload
            className="flex justify-center items-center"
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__web_token')
                ? { Authorization: `Bearer ${localStorage.__web_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'bg')}
          >
            {bgUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + bgUrl}
                alt="bg"
                className=" w-48 h-48 bg-red-400"
              />
            ) : (
              <div className="flex justify-center items-center w-48 h-48 bg-red-400">
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>
        <Form.List name="rules">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space
                  key={key}
                  style={{ display: 'flex', marginBottom: 8 }}
                  align="baseline"
                >
                  <Form.Item
                    {...restField}
                    label="分享规则"
                    name={[name, 'rule']}
                    rules={[{ required: true, message: '请输入分享规则' }]}
                  >
                    <Input placeholder="分享规则" />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  block
                  icon={<PlusOutlined />}
                >
                  添加规则
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>
        <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
          <Button type="link" htmlType="submit">
            保存
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default Share;
